div.container {
    h4 {
        color : blue;
    }

    p {
        color : green;
    }
}

@mixin 함수() {
    background : #eeeeee;
    padding: 20px;
    border-radius:5px;
    max-width : 500px;
    width: 100%;
    margin: auto;
}

.my-alert {
    @include 함수();
}

.my-alert2 {
    @extend .my-alert;
    background: yellow;
}

.my-alert p {
    margin-bottom : 0;
}

